$breakpoint-tablet: 1000px;
$breakpoint-phone: 600px;


.divesites-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    .divesite-container, .divesite-container-horizontal{
        padding: 50px 35px;
        &.divesite-col-1{flex-basis: 100%;}
        &.divesite-col-2{flex-basis: 50%;}
        &.divesite-col-3{flex-basis: 33%;}
        &.divesite-col-4{flex-basis: 25%;}
        &.divesite-col-5{flex-basis: 20%;}
    
        .divesite-horizontal{
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            flex-wrap: wrap;
            

            .divesite-horizontal-column-image{
                flex-basis: calc( 33% + 5px );
                align-self: center;
                
                .divesite-image{
                    img{
                        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                    }
                    margin: 0 auto;
                }
            }

            .divesite-horizontal-column-text{
                flex-basis: calc( 66% - 20px );
                align-self: flex-start;
                .divesite-options-container{
                    .divesite-option-title{
                        font-weight: 600;
                    }
                }
            }
        }

        .divesite-title-container{
            margin-bottom: 15px;
        }

        .divesite-image{
            img{
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            }
           // margin: 0 auto;
        } 
        .divesite-options-container{
            margin-top: 15px;
            .dashicons{
                line-height: 1.5;

            }
        }
        .divesite-description-container{
            margin-top:20px;

            .divesite-description-title{
                
            }
        }
   
    }
}


@media (min-width: $breakpoint-phone) and (max-width: $breakpoint-tablet) {
    .divesites-container{
        .divesite-col-1{flex-basis: 100%;}
        .divesite-col-2{
            flex-basis: 50%;
            &.divesite-container-horizontal{
                flex-basis:100%;
            }
        }
        .divesite-col-3{
            flex-basis: 50%;
            &.divesite-container-horizontal{
                flex-basis:100%;
            }
        }
        .divesite-col-4{
            flex-basis: 50%;
            &.divesite-container-horizontal{
                flex-basis:100%;
            }
        }
        .divesite-col-5{
            flex-basis: 50%;
            &.divesite-container-horizontal{
                flex-basis:100%;
            }
        }
    }

}
@media (max-width: $breakpoint-phone) {
    .divesites-container{
        .divesite-container-horizontal, .divesite-container{
            flex-basis:100%;
            &.divesite-col-2, &.divesite-col-3, &.divesite-col-4, &.divesite-col-5 {
                flex-basis:100%;
            }
        }
        .divesite-container-horizontal{
            .divesite-horizontal{
                .divesite-horizontal-column-image{
                    flex-basis: 100%;
                }
                .divesite-horizontal-column-text{
                    flex-basis: 100%;
                }
            }
        }

    }
  
}
